.clickItem {
    width: 32%;
    height: 13rem;
    border: 2px solid darkred;
    border-radius: 5px;
    margin: 5px;
    display: inline-block;
    cursor: pointer;
    background: gray;
    overflow: hidden;
    text-align: center;
    position: relative;
}

.clickImg {
    display: inline-block;
    margin: 0 auto;
    height: 100%;
}



